@use '/src/assets/styles/variables' as *;

.cli {
    font-family: $font-text-bash;
    font-size: $text-xl;

    input {
        font-family: $font-text-bash;
        font-size: $text-xl;
    }

    line-height: 1.5rem;

    display: flex;
    /* Hidden by default */
    position: fixed;
    /* Stay in place */
    z-index: 1;
    /* Sit on top */
    left: 0;
    top: 0;
    width: 100%;
    /* Full width */
    height: 100%;
    /* Full height */
    overflow: auto;
    /* Enable scroll if needed */
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.6);
    /* Black w/ opacity */

    &-inner {
        position: relative;
        background-color: $noir;

        width: 90%;
        height: 95vh;

        margin: auto;
        padding: 2rem;

        padding-bottom: 6rem;

        // border: 1px solid #888;
    }

    &-in {
        display: flex;
    }
}